<template>
  <div class="login">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="登录" name="first">
        <p class="message">请登录</p>
        <el-form :model="loginForm" :rules="rules" ref="loginForm" class="demo-ruleForm">
          <el-form-item prop="tel">
            <el-input placeholder="请输入登录手机号" type="tel" v-model.number="loginForm.tel"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              placeholder="请输入密码"
              type="password"
              v-model="loginForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-checkbox v-model="checked_login">下次自动登录</el-checkbox>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" round class="clearfix loginbtn" @click="login(loginForm)">登录</el-button>
          </el-form-item>
        </el-form>
        <div class="topmargin">
          <span class="fontSize">其他方式登录</span>
          <a href="#">
            <img src="../assets/weixinlogin.png" alt class="weixin" />
          </a>
        </div>
      </el-tab-pane>

      <el-tab-pane label="注册" name="second">
        <br />
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="tel">
            <el-input placeholder="请输入注册手机号" type="tel" v-model.number="ruleForm.tel"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              placeholder="请设置密码"
              type="password"
              v-model="ruleForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-checkbox v-model="checked_login">下次自动登录</el-checkbox>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" round class="clearfix loginbtn" @click="login">登录</el-button>
          </el-form-item>
        </el-form>
        <div class="topmargin">
          <span class="fontSize">其他方式登录</span>
          <a href="#">
            <img src="../assets/weixinlogin.png" alt class="weixin" />
          </a>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    var checkTel = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号不能为空"));
      } else if (!Number.isInteger(value)) {
        return callback(new Error("请输入正确格式"));
      } else if (value.toString().length != 11) {
        return callback(new Error("请输入正确格式"));
      } else {
       callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("密码不能为空"));
      } else if (value.length < 6 || value.lenth > 16) {
        return callback(new Error("密码长度应在6~16位"));
      } else {
        callback();
      }
    };
    return {
      activeName: "first",
      password: "",
      tel: "",
      checked_login: false,
      checked_agree: false,
      loginForm: {
        password: "",
        tel: ""
      },
      ruleForm: {
        password: "",
        tel: ""
      },
      rules: {
        password: [{ validator: validatePass, trigger: "blur" }],
        tel: [{ validator: checkTel, trigger: "blur" }]
      }
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    login(formName) {
      var token = this.$store.state.user.token
      var message = document.querySelector('.message')
      //登录逻辑判断入口，此处请求axios后台，根据token判断，并传到sessionStorge缓存，注册还没有细写
      this.$store.dispatch('loginFun',formName).then(res=>{
        if(token===true){
          var that =this
          setTimeout(function(){
            that.$router.push({path:'/'})
          },2100)
        }
      });
      const loadingObj=this.$loading({
        lock:true,
        text:'登录中',
        spinner:'el-icon-loading',
        background:'rgba(0,0,0,0.7)',
      })
      token = this.$store.state.user.token
      setTimeout(function(){
        if (token===true){
          loadingObj.close();
          message.className="message yes"
          message.innerHTML="登陆成功"
          
        }else{
          loadingObj.close();
          message.className="message wrong"
          message.innerHTML="手机或密码错误"
        }
      },2000)
    }
  }
};
</script>

<style>
.message{
  color: #aeb0b4;
  margin-bottom: 3px;
}
.wrong {
  color:#F56C6C;
}
.yes{
  color: green;
}
.login {
  width: 350px;
  margin: 0 auto;
}
.forgetPassword {
  float: right;
}

.clearfix {
  clear: both;
}
.loginbtn {
  width: 350px;
}

.fontSize {
  font-size: 14px;
  color: #606266;
  margin-left: 20px;
  text-align: center;
  line-height: 30px;
}
.weixin {
  width: 30px;
  float: right;
  margin-right: 20px;
}

</style>